html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url(/static/media/Roboto-Regular.3e1af3ef.ttf); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: -webkit-gradient(linear, left top, left bottom, from(#e6221a), to(#ff125b));
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.loginwithgoogle {
  width: 232px;
  color: white;
  height: 38px;
  border-radius: 10px;
  border: transparent;
  margin: 0;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f8154d), to(#e6201b));
  background-image: linear-gradient(to bottom, #f8154d, #e6201b);
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  text-align: center;
  color: #ffffff;
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
  justify-content: center;
  display: flex;
  align-items: center; }

.loginContainer {
