    background-color: rgba(238, 27, 49, 0.55);
    position: relative;
    margin: auto;
    top: 55%;
    float: right;
    right: 12%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); }
    .loginContainer .login-box .logo-container {
      width: 100%;
      height: 139px;
      position: relative; }
      .loginContainer .login-box .logo-container .logo-container-inner {
        width: 120px;
        height: 65px;
        margin: auto;
        position: relative;
        top: 31px; }
        .loginContainer .login-box .logo-container .logo-container-inner .logo-img {
          width: 100%;
          height: 100%;
          object-fit: contain; }
    .loginContainer .login-box .login-box-inner {
      padding: 0 13px 15px 13px; }
      .loginContainer .login-box .login-box-inner .user-area {
        width: 290px;
        height: 305px;
        border-radius: 10px;
        background-color: #ffffff;
        padding-left: 28px;
        padding-right: 27px;
