        .loginContainer .login-box .login-box-inner .user-area .user-input-area {
          position: relative; }
        .loginContainer .login-box .login-box-inner .user-area .user-name {
          width: 235px;
          height: 38px; }
          .loginContainer .login-box .login-box-inner .user-area .user-name input {
            width: 100%;
            height: 100%;
            border-radius: 6px;
            background-color: #ffff; }
        .loginContainer .login-box .login-box-inner .user-area .help-block {
