        .loginContainer .login-box .login-box-inner .user-area .password-area {
          padding-top: 35px;
          position: relative; }
        .loginContainer .login-box .login-box-inner .user-area .submit-area {
          padding-top: 20px;
          position: relative; }
        .loginContainer .login-box .login-box-inner .user-area .submit-button {
          width: 232px;
          height: 38px;
          border-radius: 10px;
          border: transparent;
          margin: 0;
          background-image: -webkit-gradient(linear, left top, left bottom, from(#f8154d), to(#e6201b));
          background-image: linear-gradient(to bottom, #f8154d, #e6201b);
          font-family: Roboto;
          font-size: 12px;
          font-weight: 500;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.33;
