        .loginContainer .login-box .login-box-inner .user-area .forgot-pass {
          text-align: center;
          padding-top: 15px; }
          .loginContainer .login-box .login-box-inner .user-area .forgot-pass .forgot-pass-link {
            text-decoration: none;
            font-family: Roboto;
            font-size: 14px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.14;
            letter-spacing: normal;
            text-align: left;
            color: #e61f1d;
            outline: none;
            background: transparent;
            border: none;
            cursor: pointer; }

.form-group {
  position: relative; }

.form-label {
  position: absolute;
  left: 10px;
  top: 10px;
  color: #999;
  background-color: #fff;
  z-index: 10;
  font-size: 14px;
  -webkit-transition: font-size 200ms ease-out, left 200ms ease-out, -webkit-transform 200ms ease-out;
  transition: font-size 200ms ease-out, left 200ms ease-out, -webkit-transform 200ms ease-out;
  transition: transform 200ms ease-out, font-size 200ms ease-out, left 200ms ease-out;
  transition: transform 200ms ease-out, font-size 200ms ease-out, left 200ms ease-out, -webkit-transform 200ms ease-out; }

.focused .form-label {
  -webkit-transform: translateY(-180%);
          transform: translateY(-180%);
  left: 0; }

.input-style {
  width: 100%;
  height: 100%;
  border-radius: 6px;
  border: solid 1px #d3d3d3;
  padding: 10px;
  font-size: 14px;
  line-height: 16px;
  outline: none;
  font-weight: bold;
  font-family: Roboto; }
  .input-style::-webkit-input-placeholder {
    font-weight: 500; }
  .input-style::-moz-placeholder {
    font-weight: 500; }
  .input-style:-ms-input-placeholder {
    font-weight: 500; }
  .input-style::-ms-input-placeholder {
    font-weight: 500; }
  .input-style::placeholder {
    font-weight: 500; }

.opacity {
  opacity: 0.4; }

